*{
  margin: 0;
  padding: 0;
}

.App {
  display: flex;
  height: 100%;
}


.title{
  font-weight: bolder;
  font-size: xx-large;
  color: white;
  text-decoration: none;
}

.subtitle{
  color: #999;
}

.contents{
  padding: 30px 0 0 0;
  color: black;
}

.header {
  width: 230px;
  background-color: #555;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
  color: white;
  padding: 40px;
}

.container {
  padding: 20px 1500px 20px 100px;
  background-color: #333;
}

.routersBox {
  min-height: 100vh;
  margin-left: 20px;
}
.mainTxt{
  width: 100%;
  padding: 100px 50px;
  color: #fff;
}

.mainTxt h2 {
  font-size: 80px;
  font-family: 'Times New Roman';
}

.body {
  display: block;
  padding: 10px;
  background-color: #555;
  width: 200px;
  color:white;
  flex-wrap: wrap;
  
}

.body a {
  font-size: 20px;
  padding: 8px 15px;
  margin-right: 10px;
  text-decoration: none;
  color: #333;
  font-weight: bold;
  white-space: nowrap;
}

.body a:hover {
  color: white;
}


.outer {
  width: 800px;
  margin-top: 20px;
  padding: 20px;
  background-color: #333;
}



.list-table {
  width: 800px;
  border-collapse: collapse;
  margin-top: 20px;
}
.list-table th, .list-table td {
  border: 1px solid #ddd;
  padding: 8px;
  text-align: center;
}
.list-table th {
  background-color: #666;
}
.list-table td {
  background-color: #F2F2F2;
}
.list-table tbody tr:hover {
  background-color: white;
  cursor: pointer;
}
.list-table button {
  background-color: #666;
  color: white;
  border: none;
  padding: 6px 10px;
  cursor: pointer;
}
.list-table button:hover {
  background-color: #333;
}






.detail-container {
  max-width: 600px;
  margin: 0 auto;
  padding: 20px;
  text-align: center;
  border: 2px solid white;
}

.detail-container h2 {
  color: white;
}

.movie-detail {
  margin-top: 20px;
}

.detail-name {
  font-weight: bold;
  margin-right: 5px;
  color: white;
}
.name-value{
  color: white;
}

.detail-content {
  margin-top: 20px;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 5px;
  background-color: #f9f9f9;
  overflow: auto;
}





.insert-table input{
  border-radius: 5px;
  width: 100%;
  padding: 10px 0;
}

.insert-form-container {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
}

.insert-table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px;
}

.insert-table th, .insert-table td {
  padding: 4px 8px 4px 4px;
  border: 1px solid #ddd;
}

.insert-table th {
  background-color: #f2f2f2;
}

textarea {
  width: 100%;
  height: 100%;
  padding: 25px;
  border: 1px solid #ddd;
  border-radius: 5px;
  box-sizing: border-box;
  resize: none;
}

button {
  background-color: #555;
  color: #fff;
  padding: 10px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

button:hover {
  background-color: #333;
}
